.homePage .headerSection .appName {
  font-size: 3rem;
  margin: 20px auto;
  display: flex;
  justify-content: center;
}

.homePage .teamGrid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}

.homePage .winner,
.twoTeams {
  width: inherit;
  text-align: right;
  margin-top: 20px;
}

@media (max-width: 550px) {
  .homePage .teamGrid {
    grid-template-columns: 1fr;
  }
}
