.winnerCard {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 30px auto;
  padding: 15px 0;
}

.winnerCard .leftSide {
  margin: 0 15px;
}

.winnerCard .leftSide h1 {
  font-size: 3rem;
}

.winnerCard .leftSide .trophy {
  height: 45px;
}

.winnerCard .leftSide h2,
h3,
p {
  margin: 10px 0;
}

.winnerCard .rightSide {
  text-align: right;
  margin-right: 20px;
}

.winnerCard .rightSide h3 {
  margin-top: 20px;
}

@media (max-width: 760px) {
  .winnerCard {
    grid-template-columns: 1.5fr 1fr;
  }
}

@media (max-width: 640px) {
  .winnerCard {
    display: block;
  }

  .winnerCard .rightSide {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  .winnerCard .rightSide h3 {
    text-align: left;
    margin: 10px 15px;
  }

  .winnerCard .rightSide p {
    text-align: left;
  }
}

@media (max-width: 400px) {
  .winnerCard .rightSide {
    grid-template-columns: 1fr 1fr;
  }
}
