.twoTeam {
  width: inherit;
}

.twoTeam .dropDown {
  display: flex;
  justify-content: center;
  align-items: center;
}

.twoTeam .dropDown select {
  padding: 7px;
  font-size: 1.2rem;
  border-radius: 13px;
  border: none;
}

.twoTeam .dropDown .vs {
  margin: 0 30px;
  font-size: 2.2rem;
}

.twoTeam .dropDown .btn {
  padding: 7px 18px;
  margin-left: 30px;
  font-size: 1.2rem;
  background-color: transparent;
  border: 1px solid white;
  color: white;
  border-radius: 10px;
}

.twoTeam .dropDown .btn:hover {
  background-color: #0e1d32;
  border: 1px solid #0e1d32;
}

.twoTeam .msg {
  width: inherit;
  text-align: center;
  margin: 20px 0;
  font-size: 2.5rem;
}

@media (max-width: 850px) {
  .twoTeam .dropDown select {
    padding: 5px;
    width: 30vw;
  }

  .twoTeam .dropDown .vs {
    font-size: 1.7rem;
  }

  .twoTeam .dropDown .btn {
    padding: 6px 10px;
    font-size: 1rem;
  }
}

@media (max-width: 650px) {
  .twoTeam .dropDown {
    display: block;
    width: 100vw;
  }
  .twoTeam .dropDown select {
    padding: 8px 4px;
    margin: 15px 4vw;
    width: 80vw;
  }
  .twoTeam .dropDown .vs {
    width: 90vw;
    margin: 0px;
    text-align: center;
  }
  .twoTeam .dropDown .btnDiv {
    width: 85vw;
    margin: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .twoTeam .dropDown .btnDiv .btn {
    padding: 9px 18px;
  }
}
