.simpleMatchCard {
  width: inherit;
  margin: 20px 0;
  border: 2px solid white;
}

.simpleMatchCard .teamNames {
  width: inherit;
  margin: 10px 0;
  display: flex;
  justify-content: center;
}

.simpleMatchCard .teamNames .vs {
  margin: 9px 20px;
}

.simpleMatchCard .matchDate,
.venue {
  text-align: end;
  margin-right: 20px;
}

.simpleMatchCard .additionalDetail {
  display: flex;
  justify-content: space-around;
}

.simpleMatchCard .result {
  text-align: center;
  margin: 7px 0;
}

@media (max-width: 780px) {
  .simpleMatchCard .teamNames {
    display: grid;
    grid-template-columns: 1fr 0.5fr 1fr;
    margin: 10px 3vw;
  }
}

@media (max-width: 650px) {
  .simpleMatchCard .additionalDetail {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-left: 12vw;
  }
}

@media (max-width: 500px) {
  .simpleMatchCard .teamNames {
    grid-template-columns: 1fr;
  }
  .simpleMatchCard .teamNames h1,
  span {
    text-align: center;
  }
  .simpleMatchCard .additionalDetail {
    margin-left: 0;
  }
}
