.teamPage {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 0.5fr;
  gap: 10px;
}

.teamPage .teamNameSection {
  grid-column: span 3;
  justify-self: start;
  align-self: end;
}

.teamPage .teamNameSection .teamName {
  font-size: 4rem;
  margin-bottom: 20px;
}

.teamPage .winLossSection {
  justify-self: center;
  padding: 30px;
  text-align: center;
}

.teamPage .matchDetailSection {
  grid-column: span 4;
}

.teamPage .moreLink {
  justify-self: center;
  align-self: center;
  font-size: 1.5rem;
}

@media (max-width: 650px) {
  .teamPage {
    grid-template-rows: 1fr 1fr 0.4fr 0.4fr;
  }
  .teamPage .teamNameSection {
    grid-column: span 2;
    align-self: center;
  }

  .teamPage .winLossSection {
    grid-column: span 2;
  }

  .teamPage .moreLink {
    grid-column: span 2;
  }
}

@media (max-width: 600px) {
  .teamPage {
    grid-template-rows: 0.5fr 0.1fr 0.8fr 0.4fr 0.4fr;
  }
  .teamPage .teamNameSection {
    grid-column: span 4;
  }

  .teamPage .winLossSection {
    grid-column: span 4;
  }
}
