.matchDetailCard {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 10px;
  margin-bottom: 20px;
}

.matchDetailCard .matchDate,
.matchVenue,
.matchResult {
  margin-top: 10px;
}

.matchDetailCard .additionalDetail {
  text-align: right;
}

.matchDetailCard .additionalDetail p {
  margin-bottom: 25px;
}
