.matchPage {
  display: grid;
  grid-template-columns: 1fr 4fr;
}

.matchPage .yearSelector {
  justify-self: end;
  align-self: right;
  padding: 20px;
}

.matchPage .yearSelector h3 {
  text-align: end;
}

.matchPage .pageHeading {
  margin: 10px 0;
}

@media (max-width : 550px) {
  .matchPage {
    grid-template-columns: 0.5fr 5fr;
  }

  .matchPage .yearSelector {
    padding: 0 10px;
  }
}
